*
{
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: gold;
}
body
{
  background-image: url(https://as2.ftcdn.net/v2/jpg/03/45/88/07/1000_F_345880772_zIT2mkdCzTthplO7xqaGGrMspN0jw0ll.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display : flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
}
.header
{
  color: deeppink;
  text-align: center;
  font-size: 40px;
}
.app
{
  background: linear-gradient(5deg, rgb(250, 57, 208) 0%,blue 100%);
  width: 90%;
  min-height:200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  box-shadow: 5px 5px 50px 0px deeppink;
  display: flex;
  justify-content: space-evenly;
}
.question-section
{
  color: royalblue;
  font-size: 30px;
  position: relative;
  width: 100%;
}
.question-count
{
  margin-bottom: 20px;
  font-size: 15px;
}
h1
{
  color: fuchsia;
}
.question-text
{
  margin-bottom: 12px;
}
.answer-section
{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button
{
  width: 100%;
  font-size: 16px;
  color: white;
  background: linear-gradient(5deg, blue 0%,rgb(250, 57, 208) 100%);
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 3px solid blue;
  cursor: pointer;
}
.score-section 
{
  font-size: 35px;
  color: white;
}
a
{
  border: 1px solid black;
  border-radius: 30px;
  font-size: 30px;
  background-color: green;
  color: white;
  text-decoration: none;
  padding: 10px;
  margin-top: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
a:hover
{
  background-color: deeppink;
}